import React, { Component } from 'react';
import { Row, Col, Nav, NavItem, NavLink, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Calender from './../components/Calender';
import * as RoomApi from '../services/room';

class Home extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      rooms: [],
      roomId: null,
      modal: false
    };
    this.toggle = this.toggle.bind(this);
  }
  componentDidMount() {
    RoomApi.findAll()
      .then(res => {
        this.setState({ rooms: res.data.data });
        if (this.state.rooms.length > 0) { //   null     
          this.changeRoom(this.state.rooms[0].id, this.state.rooms[0].name);
        }
      })
      .catch(err => {
        console.log(err);
      });
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  changeRoom(id, name) {
    this.calender.setEvents(id, name);
  }
  render() {
    return (
      <div style={{ 'padding': '20px 0px 20px 0px' }}>
        <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          <ModalHeader>   </ModalHeader>
          <ModalBody>
            <ul>
              <li>    .</li>
              <li>     .</li>
              <li>  ,   .</li>
              <li>       .</li>
              <li>     ,   count  .</li>
              <li className='text-danger'>  08:00 ~ 19:30 / .</li>
              <li className='text-danger'>  30 .</li>
              <li className='text-danger'>   10 .</li>
            </ul>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggle}>Cancel</Button>
          </ModalFooter>
        </Modal>
        <Row>
          <Col>
            <Nav>
              {this.state.rooms.map((room, i) => {
                return (
                  <NavItem key={i}>
                    <NavLink href='#' onClick={(e) => this.changeRoom(room.id, room.name, e)} >{room.name}</NavLink>
                  </NavItem>
                );
              })}
            </Nav>
          </Col>
          <Col>
            <Nav className="float-right">
              <NavItem>
                <NavLink href='#' onClick={this.toggle} ></NavLink>
              </NavItem>
            </Nav>
          </Col>
        </Row>
        <hr />
        <Calender onRef={ref => (this.calender = ref)} />
      </div>
    );
  }
}

export default Home;



